* {
  @include box-sizing;
  line-height: 2;
}

html,
body {
  color: $default-color;
  background-color: #fff;
  margin: 0;
  padding: 0;
}

html {
  font-family: 'Hiragino Kaku Gothic Pro','ヒラギノ角ゴ Pro W3','メイリオ',Meiryo,'ＭＳ Ｐゴシック',sans-serif;
  font-size: 14px;
  overflow-y: scroll;

  @media (min-width: 600px) {
    font-size: 16px;
  }
}

body {
  -webkit-text-size-adjust: 100%;
  background-color: #AAA;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: $default-shade;
  line-height: normal;
}

h1 {
  font-size: 20px;
}

h2 {
  // font-size: 20px;
  padding: .2em .5em;
  color: #494949;
  background: #dae6f1;
  border-left: solid 5px #7db4e6;
  border-bottom: solid 3px #d7d7d7;
  margin-top: 30px;
}

a {
  color: $blue;
  text-decoration: none;
}

blockquote {
  border-left: .25rem solid $grey-2;
  color: $grey-1;
  background-color: lightyellow;
  margin: .8rem 0;
  padding: .5rem 1rem;

  p:last-child {
    margin-bottom: 0;
  }

  @media (min-width: 600px) {
    padding: 0 5rem 0 1.25rem;
  }
}

img {
  display: block;
  margin: 0 0 1rem;
  max-width: 100%;
}

table, tr, td, th {
  border-spacing: 0;
  border-collapse: collapse;
  border:1px solid #333;
}

td, th {
  vertical-align: top;
  padding: 5px;
}

pre {
	white-space: pre-wrap;
  word-break:break-all;
}
