.container {
  display: flex;
  margin: 10px 15px;
  gap: 10px
}

.footer-container,
.nav-container {
  margin: 0 auto;
  width: 95%;

  .search-box {
    input[type=search] {
      display: block;
      width: 250px;
      margin-top: 10px;
      padding: 5px;
      border: 1px solid #333333;
      border-radius: 5px;
    }
    input[type=submit] {
      display: block;
      margin-top: 10px;
      margin-left: 5px;
      padding: 5px 10px;
      border: none;
      border-radius: 5px;
    }
  }
}

.nav {
  box-shadow: 0 2px 2px -2px $shadow-color;
  overflow: auto;
  background: #FFFFFF;
  padding: 10px 0;

  &-container {
    margin: 5px auto;
    position: relative;
    text-align: center;
  }

  &-title {
    @include transition(all .2s ease-out);
    color: #fff;
    font-size: 2rem;
    font-weight: 700;
    font-family: Georgia,'Times New Roman',Times,serif;
    display: inline-block;
    margin: 0;
    padding-right: .2rem;

    &:hover,
    &:focus {
      opacity: .6;
    }
  }

  ul {
    list-style-type: none;
    margin: 1rem 0 0;
    padding: 0;
    text-align: center;
  }

  li {
    @include transition(all .2s ease-out);
    color: $default-color;
    display: inline-block;
    opacity: .6;
    padding: 0 2rem 0 0;

    &:last-child {
      padding-right: 0;
    }

    &:hover,
    &:focus {
      opacity: 1;
    }
  }

  .header-logo {
    width: auto;
    height: 50px;
    margin: 8px;
  }
}

@media (min-width: 600px) {
  .nav {
    &-container {
      text-align: left;
    }

    ul {
      bottom: 0;
      position: absolute;
      right: 0;
    }
  }
  .nav-container {
    display: flex;
  }
}

main {
  width: calc(100% - 300px);
  min-height: 100vh;
}

#sidebar {
  width: 300px;
  height: 600px;
  background: #FFFFFF;
  padding: 10px;
  margin-top: 10px;
  border-radius: 5px;
  h4 {
    font-weight: bolder;
    font-size: x-large;
    margin: 10px 0;
  }
  span.title {
    font-weight: bold;
  }
  p {
    line-height: 1em;
    margin: 0
  }
  ul {
    margin: 0;
  }
  p.description {
    line-height: 1.2em;
    font-style: italic;
  }
  div.icon {
    img {
      width: 100%;
      height: auto;
      padding: 10px 15px 5px;
      margin: 0;
    }
  }
  div.sns {
    display: flex;
    gap: 10px;
    img {
      width: 40px;
      height: auto;
      border-radius: 45px;
    }
  }
}
@media (max-width: 1200px) {
  main {
    width: 100%;
  }
  #sidebar {
    display: none;
  }
}

footer {
  margin-top: 15px;
  padding: 5px 0;
  text-align: center;
  background: #FFFFFF;
  color: #121212;
  font-size: 16px;
  font-family: Georgia,'Times New Roman',Times,serif;
}

.fx {
  display: flex;
}

.jc-sb {
  justify-content: space-between;
}
