.catalogue {
  &-item {
    background-color: #FFF;
    padding: 20px;
    margin-top: 10px;
    border-radius: 10px;
    color: $default-color;
    display: block;
    text-decoration: none;

    &:hover .catalogue-line,
    &:focus .catalogue-line {
      width: 5rem;
      text-decoration: none;
    }
  }

  .publish-date {
    font-weight: bold;
    margin: 0 0 5px;
  }

  &-title {
    display: block;
    color: #2eae9b;
    font-size: 26px;
    font-weight: 700;
    margin: .5rem 0;
  }

  &-line {
    @include transition(all .3s ease-out);
    border-top: .2rem solid $default-shade;
    display: block;
    width: 2rem;
    margin-bottom: 10px;
  }

  .tags_list {
    display: flex;

    .tags_item {
      a {
        background-color: #707070;
        color: #fff;
        padding: 2px 9px;
        margin-right: 5px;
        border-radius: 5px;
        font-size: 14px;
      }
    }
  }
}
