.post {
  padding: 20px;
  margin-top: 10px;
  background-color: #FFF;
  border-radius: 10px;

  .publish-date {
    font-weight: bold;
  }

  &-title {
    font-size: 26px;
    margin: 1rem 0;
  }

  &-line {
    border-top: 0.4rem solid $default-shade;
    display: block;
    margin: 0 auto 3rem;
    width: 4rem;
  }

  p {
    margin: 0 0 1rem;
    text-align: justify;
  }

  a:hover {
    text-decoration: underline;
  }

  img {
    box-shadow: 0 0 3px 1px rgba(0,0,0,0.5);
  }

  .tags_list {
    display: flex;

    .tags_item {
      a {
        background-color: #707070;
        color: #fff;
        padding: 2px 9px;
        margin-right: 5px;
        border-radius: 5px;
        font-size: 14px;
      }
    }
  }
}
