pre,
code {
  font-family: $monospaced;
}

code {
  color: $code-color;
  border: 1px solid $default-tint;
  border-radius: 3px;
  font-size: 85%;
  padding: 2px 5px;
  white-space: pre-wrap;
}

pre {
  background-color: #333;
  margin: 5px;
  padding: 5px;
}

pre code {
  border: 0;
  color: #FFF;
  font-size: 100%;
  padding: 0;
}

.highlight {
  background-color: $grey-3;
  border-radius: 3px;
  line-height: 1.4;
  margin: 0 0 1rem;
  padding: 1rem;

  pre {
    margin-bottom: 0;
    overflow-x: auto;
  }

  .lineno {
    color: $default-tint;
    display: inline-block; // Ensures the null space also isn't selectable
    padding: 0 .75rem 0 .25rem;
    // Make sure numbers aren't selectable
    -webkit-user-select: none;
       -moz-user-select: none;
            user-select: none;
  }
}
